<template>
  <div class="kanban">
    <div class="titleHead">
      <p id="pHead">{{kanbanConfig.title}}</p>
      <p id="time">{{ time }}</p>
    </div>
    <div class="kanbanContent">
      <div class="rightContent">
        <div class="right2" v-for="(item, index) in dataList" :key="index" >
          <div class="right2title">
            {{item.deviceName}}
            <div :class="devClass(item)" >{{devStateName(item)}}<div></div></div>
          </div>
          <div class="rightCard" >
              <div class="card card1" >
                <p>{{valueFunc1(item.dayEnergyConsumption)}}</p>
                <p>当日流量</p>
              </div>
              <div class="card card2" >
                <p>{{valueFunc1(item.allConsumption)}}</p>
                <p>累计流量</p>
              </div>
          </div>
          <div class="tableDiv">
            <div class="valueDiv" v-for="(itemx, indexx) in item.xyVoList" :key="indexx" >
              <div class="valueDivx" v-show="xyIf(indexx,item.xyVoList)" ><span>{{strFunc(itemx.x)}}时：</span><span>{{valueFunc(itemx.y)}}</span></div>
            </div>
            <!-- <table>
              <thead>
                <tr>
                  <th>时间</th>
                  <th>用量</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(itemx, indexx) in item.xyVoList" :key="indexx" >
                  <td>{{itemx.x}}</td>
                  <td>{{valueFunc(itemx.y)}}</td>
                </tr>
              </tbody>
            </table> -->
          </div>
          <!-- <div class="right2content"  ref="echarts"></div> -->
          <!-- <div class="line"></div> -->
        </div>
      </div>
    </div>
  </div>
</template>

