      this.showEchartsData();
    }, this.kanbanConfig.time);

    // setInterval(() => {
    this.getDataTime();
    // }, 1000);
  },
  created() {
  },
  methods: {
    devStateName(param) {
      if (param.deviceRealState === 1) {
        return '在线';
      } else if (param.deviceRealState === 3) {
        return '离线';
      }
    },
    strFunc(param) {
      return param.substring(11, 13);
    },
    xyIf(index, list) {
      // if (list.length <= 12) {
      //   // console.log('当前数据少于12', list);
      //   return true;
      // } else {
      //   if (this.xyIndex === 0) {
