      //     // console.log('bbb', this.xyIndex, index);
      //     if (index >= 12) {
      //       return true;
      //     } else {
      //       return false;
      //     }
      //   }
      // }
      return true;
    },
    devClass(param) {
      if (param.deviceRealState === 1) {
        return 'devCricle run';
      } else if (param.deviceRealState === 3) {
        return 'devCricle noLine';
      }
    },
    valueFunc(param) {
      if (!Number(param)) {
        return 0;
      } else {
        return Number(Number(param).toFixed(3));
      }
    },
    async showEchartsData() {
      // var that = this;
      try {
        var param = {
          startTime: '',
          endTime: ''
        };
        var nowHour = new Date(this.dataTime).getHours();
        console.log(nowHour);
        if (nowHour < 7) {
          param.startTime = this.timeFunc(this.dataTime - (1000 * 60 * 60 * 12), '19');
          param.endTime = this.timeFunc(this.dataTime, '07');
        } else if (nowHour > 19) {
          param.startTime = this.timeFunc(this.dataTime, '19');
          param.endTime = this.timeFunc(this.dataTime + (1000 * 60 * 60 * 12), '07');
        } else {
          param.startTime = this.timeFunc(this.dataTime, '07');
          param.endTime = this.timeFunc(this.dataTime, '19');
        }
        console.log(param);
        const res = await getGasHoursUseReportBoard(param,
          {
            secretKey: this.kanbanConfig.key
          }
        );

        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.dataList = res.rows;

          for (let i = 0; i < this.dataList.length; i++) {
            // ((i) => { this.dataList[i].xyVoList.reverse(); })(i);
            // this.dataList[i].xyVoList = [
            //   { x: '2022-11-28 00:00:00', y: 23.85 },
            //   { x: '2022-11-28 01:00:00', y: 23 },
            //   { x: '2022-11-28 02:00:00', y: 23 },
            //   { x: '2022-11-28 03:00:00', y: 23 },
            //   { x: '2022-11-28 04:00:00', y: 23 },
            //   { x: '2022-11-28 05:00:00', y: 23 },
            //   { x: '2022-11-28 06:00:00', y: 23 },
            //   { x: '2022-11-28 07:00:00', y: 23 },
            //   { x: '2022-11-28 08:00:00', y: 23 },
            //   { x: '2022-11-28 09:00:00', y: 23 },
            //   { x: '2022-11-28 10:00:00', y: 23 },
            //   { x: '2022-11-28 11:00:00', y: 23 },
            //   { x: '2022-11-28 12:00:00', y: 23 },
            //   { x: '2022-11-28 13:00:00', y: 23 },
            //   { x: '2022-11-28 14:00:00', y: 23 },
            //   { x: '2022-11-28 15:00:00', y: 23 },
            //   { x: '2022-11-28 16:00:00', y: 23 },
            //   { x: '2022-11-28 17:00:00', y: 23 },
            //   { x: '2022-11-28 18:00:00', y: 23 },
            //   { x: '2022-11-28 19:00:00', y: 23 },
            //   { x: '2022-11-28 20:00:00', y: 23 },
            //   { x: '2022-11-28 21:00:00', y: 23 },
            //   { x: '2022-11-28 22:00:00', y: 23 },
            //   { x: '2022-11-28 23:00:00', y: 23 },
            //   { x: '2022-11-28 24:00:00', y: 23 }
            // ];
            // if (this.dataList[i].xyVoList.length > 12) {
            //   if (this.xyIndex === 0) {
            //     this.dataList[i].xyVoList = { ...this.dataList[i].xyVoList.splice(0, 12) };
            //   } else {
            //     this.dataList[i].xyVoList = { ...this.dataList[i].xyVoList.splice(12, this.dataList[i].xyVoList.length) };
            //   }
            // }
            this.dataList[i].dayEnergyConsumption = Number(this.dataList[i].dayEnergyConsumption.toFixed(3));
            this.dataList[i].allConsumption = Number(this.dataList[i].allConsumption.toFixed(3));
          }

          // setTimeout(() => {
          //   if (that.xyIndex === 0) {
          //     that.xyIndex = 1;
          //     console.log('后续访问1', that.xyIndex);
          //   } else {
          //     that.xyIndex = 0;
          //     console.log('后续访问2', that.xyIndex);
          //   }
          // }, 1000);
          // setTimeout(() => {
          //   this.dataList = dataList;
          // }, 300);

          // this.$nextTick(() => {
          // for (let i = 0; i < this.dataList.length; i++) {
          // var time = [];
          // var value1 = [];
          // var value2 = [];
          // var value1Num = 0;

          // ((i) => { this.dataList[i].xyVoList.reverse(); })(i);

          // if (window.screen.width <= 1024) {
          //   dataList[i].xyVoList = dataList[i].xyVoList.splice(4);
          // } else {
          //   dataList[i].xyVoList = dataList[i].xyVoList.splice(6);
          // }

          // this.dataList[i].dayEnergyConsumption = Number(this.dataList[i].dayEnergyConsumption.toFixed(3));
          // this.dataList[i].allConsumption = Number(this.dataList[i].allConsumption.toFixed(3));

          // for (var x = 0; x < this.dataList[i].xyVoList.length; x++) {
          //   var param = this.dataList[i].xyVoList[x];
          //   time.push(param.y.substring(10, 16));
          //   if (param.x) {
          //     value1.push(Number(param.x));
          //     value1Num += Number(param.x);
          //   } else {
          //     value1.push(0);
          //   }
          // }
          // for (var y = 0; y < this.dataList[i].xyVoList.length; y++) {
          //   if (value1Num === 0) {
          //     value2.push(0);
          //   } else {
          //     value2.push((value1Num / this.dataList[i].xyVoList.length));
          //   }
          // }
          // this.drawOne(
          //   time,
          //   value1,
          //   value2,
          //   this.$refs.echarts[i]
          // );
          // }
          // this.dataList = dataList;
          // });
        } else {
        }
      } catch (error) {
      } finally { }
    },
    setItemRef(el) {
      this.refList.push(el);
    },
    // 绘制单产线能耗
    drawOne(time, today, yesterday, chartRef) {
      var fontSizJson = {
        a: 12,
        b: 35,
        c: 18
      };

      if (window.screen.width <= 1024) {
        fontSizJson = {
          a: 10,
          b: 25,
          c: 8
        };
      } else if (window.screen.width <= 1280) {
        fontSizJson = {
          a: 12,
          b: 20,
          c: 12
        };
      }

      const lineChart = this.$echarts.init(chartRef);
      const option = {
        color: ['#ddd', '#fff'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: 20,
          right: 50,
          top: 30,
          bottom: 5,
          containLabel: true
        },
        legend: {
          // data: [],
          right: '20px',
          top: '10px',
          icon: 'rect',
          // align: 'center',
          itemGap: 20,
          itemWidth: 20,
          itemHeight: 6,
          textStyle: {
            color: '#fff',
            fontSize: fontSizJson.a
            // rich: rich
          }
        },
        calculable: true,
        xAxis: [{
          name: '时间',
          nameTextStyle: {
            color: '#929ee6',
            fontSize: fontSizJson.a,
            textShadowColor: '#000',
            textShadowOffsetY: 2
          },
          boundaryGap: false,
          type: 'category',
          data: time,
          axisLine: {
            show: true,
            lineStyle: {
              width: 2,
              color: '#2077a3'
            },
            symbol: ['none', 'arrow']
          },
          axisLabel: {
            margin: 10,
            color: '#929ee6',
            fontSize: fontSizJson.a,
            // fontWeight: 'bold',
            textShadowColor: '#000',
            textShadowOffsetY: 2
          }
        }],
        yAxis: {
          name: 'm³',
          nameTextStyle: {
            color: '#929ee6',
            fontSize: fontSizJson.a,
            textShadowColor: '#000',
            textShadowOffsetY: 2
          },
          type: 'value',
          splitLine: {
            show: false,
            lineStyle: {
              color: 'rgba(255,255,255,.2)'
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              width: 2,
              color: '#2077a3'
            },
            symbol: ['none', 'arrow']
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            color: '#929ee6',
            fontSize: fontSizJson.a,
            // fontWeight: 'bold',
            textShadowColor: '#000',
            textShadowOffsetY: 2
          },
          data: []
        },
        series: [{
          name: '瞬时流量',
          type: 'line',
          smooth: true, // 是否平滑
          showAllSymbol: true,
          symbol: 'circle',
          symbolSize: 0,
          label: {
            show: false,
            position: 'top',
            textStyle: {
              color: '#1fd5d7'
            }
          },
          itemStyle: {
            color: '#1fd5d7',
            borderWidth: 3,
            shadowColor: 'rgba(0, 0, 0, .3)',
            shadowBlur: 0,
            shadowOffsetY: 2,
            shadowOffsetX: 2
          },
          tooltip: {
            show: false
          },
          areaStyle: {
            normal: {
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: 'rgba(31,213,215,0.7)'
              },
              {
                offset: 1,
                color: 'rgba(31,213,215,0)'
              }
              ], false),
              shadowColor: 'rgba(31,213,215, 0.9)',
              shadowBlur: 20
            }
          },
          data: today
        },
        {
          name: '平均流量',
          type: 'line',
          smooth: true, // 是否平滑
          showAllSymbol: true,
          // symbol: 'image://./static/images/guang-circle.png',
          symbol: 'circle',
          symbolSize: 0,
          lineStyle: {
            normal: {
              color: '#FF6B72',
              shadowColor: 'rgba(0, 0, 0, .3)',
              shadowBlur: 0,
              shadowOffsetY: 5,
              shadowOffsetX: 5
            }
          },
          label: {
            show: false,
            position: 'top',
            textStyle: {
              color: '#FF6B72'
            }
          },
          itemStyle: {
            color: '#FF6B72',
            borderWidth: 3,
            shadowColor: 'rgba(0, 0, 0, .3)',
            shadowBlur: 0,
            shadowOffsetY: 2,
            shadowOffsetX: 2
          },
          tooltip: {
            show: false
          },
          data: yesterday
        }
        ]
      };
      lineChart.setOption(option, true);
      window.addEventListener('resize', () => {
        lineChart.resize();
      });
    },
    // 看板时间
    getDataTime() {
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      console.log('时间', $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date'));
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    timeFunc(param, str) {
      var date = new Date(param);
      var y = date.getFullYear();
      var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      // var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      // var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
      // var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
      return y + '-' + M + '-' + d + ' ' + str + ':00:00';
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    valueFunc1(param) {
      if (param) {
        return param;
      } else {
        return 0;
      }
    }
  }
};
</script>

<style lang="less" scoped>
